import React, { Component } from 'react';

class ExcelTable extends Component {
  
  render(){
   let keys=this.props.colSelected;   
    return (
      <div 
      style={{"display":"none"}}
      >
        <table className="table" id="excel-table">

        <TableHeader colSelected={this.props.colSelected} newKeys={this.props.newKeys}
        colKeys={this.props.colKeys}/>
        
        <tbody>
        {this.props.list.map((value,index)=>
                                  { 
                                      if(value.selected){ 
                                      let obj=value.added_by[0]
                                      console.log(obj) 
                                    let statusList = obj.status;
                                       let finalStatus = statusList[statusList.length - 1].status,
                                       date=statusList[statusList.length-1].Timestamp;
                                       finalStatus=finalStatus.toString();
                                       if((finalStatus===""||finalStatus == null||finalStatus.includes("null")||finalStatus=="NaN")
                                   && statusList.length>1){
                                           finalStatus = statusList[statusList.length - 2].status;
                                           date=statusList[statusList.length-2].Timestamp;
                                       }
                                       finalStatus=finalStatus.split(":");
                                     
                                      return (
                                  <tr key={index}>
                                      {
                                          this.props.colKeys.map((elem,index)=>{
                                              let td=null;
                                              if(keys[elem])
                                              switch(elem){
                                                case "Status":td=<td key={index}>{finalStatus[0]+' : '+finalStatus[1]+' '}</td>;
                                                            break;
                                                case "Last Action":td=<td key={index}>{new Date(parseInt(date)).toLocaleDateString()}</td>;
                                                              break;
                                                case "Name":td=<td key={index}>{value.candidateName}</td>;
                                                    break;
                                                case "Mail Id":td=<td key={index}>{value.candidateEmail}</td>;break;
                                                case "Mobile":td=<td key={index}>{value.candidateContact}</td>;break;
                                                case "Recruiter":td=<td style={{textTransform:"capitalize"}} key={index}>{obj.userName}</td>;break;
                                                case "Company":td= <td key={index}>{value.work_experience[0].organization}</td>;break;
                                                case "Designation":td=<td key={index}>{value.work_experience[0].role}</td>;break;
                                                case "Experience":td=<td key={index}>{value.experience}</td>;break;
                                                case "CTC":td=<td key={index}>{value.current_ctc}</td>;break;
                                                case "ETEC":td=<td key={index}>{value["et/ec"]}</td>;break;
                                                case "Notice Period":td=<td key={index}>{value.noticePeriod}</td>;break;
                                                case "Univesity":td=<td key={index}>{value.college}</td>;break;
                                                case "Degree":td=<td key={index}>{value.degree}</td>;break;
                                                case "Location":td=<td key={index}>{value.location}</td>;break;
                                                default:td=<td key={index}>{value[elem]}</td>;
                                              }
                                              return td;
                                          })
                                      }
                                  </tr>
                                      );
                                    }
                                    
                                      else 
                                      return null;
                                  })
                                  }
                              </tbody>
        <TableFooter colSelected={this.props.colSelected} newKeys={this.props.newKeys}
        colKeys={this.props.colKeys}/>
        </table>
      </div>
    );
  }
}

export default ExcelTable;


const TableFooter= (props)=>{
  let obj=props.colSelected,headers=[],newKeys=props.newKeys,colKeys=props.colKeys;
  for(let x in colKeys){
    if(obj[colKeys[x]]===true){
        headers.push(colKeys[x]);
    }
}

    return (
        <tfoot>
        <tr>  
          {headers.map((value,index)=>{
            return <th key={index}>{value}</th>
          })}
        </tr>
        </tfoot>
    );
}
const TableHeader =(props)=>{
  let obj=props.colSelected,headers=[],newKeys=props.newKeys,colKeys=props.colKeys;
    for(let x in colKeys){
        if(obj[colKeys[x]]===true){
            headers.push(colKeys[x]);
        }
    }
    return (
        <thead>
        <tr>  
          {headers.map((value,index)=>{
            return <th key={index}>{value}</th>
          })}
        </tr>
        </thead>
    );
}


