selectAll(lower,upper){
  // console.log(lower,upper)
  let list =this.state.candidateList.slice();
  list=list.filter(function(obj,index){
    if(index<=upper&&index>=lower)
    obj.selected=true;
    return true;
  });
  this.setState({candidateList:list});
}

deselectAll(){
  let list =this.state.candidateList.slice();
  list=list.filter(function(obj){
    obj.selected=false;
    return true;
  });
  // console.log(list);
  this.setState({candidateList:list});
}
  

