addColumn(colName){
		let exist = this.state.newKeys.findIndex((element)=>{
			return element.toLowerCase() === colName.toLowerCase();
		});
		if(exist === -1){
			exist = this.state.initialKeys.findIndex((element)=>{
				return element.toLowerCase() === colName.toLowerCase();
			});
		}
		if(exist === -1){
      let newCol = JSON.parse(JSON.stringify(this.state.selectedCol));
      let activeJob=JSON.parse(JSON.stringify(this.state.activeJob));
      let newKeys=JSON.parse(JSON.stringify(this.state.newKeys));
      let col=JSON.parse(JSON.stringify(this.state.colKeys));
      let domain=atob(localStorage.getItem("token")).split(":")[0].split("@")[1].split(".")[0];
      
      // debugger;
      if(activeJob.added_col&&activeJob.added_col[domain]){
         let arr=activeJob.added_col[domain];
          arr.push(colName);
          activeJob.added_col[domain]=arr;
          this.updateJob(activeJob,null,null); 
      }
      else { 
        if(!activeJob.added_col)
        activeJob.added_col={};
        activeJob.added_col[domain]=[colName];
        newKeys=[colName];
        this.updateJob(activeJob,null,null); 
      }

      col.push(colName);
      newCol[colName] = true;
      let list=JSON.parse(JSON.stringify(this.state.candidateList));
      for(let x in list){
        list[x][colName]=list[x][colName]==undefined?"":list[x][colName];
        this.updateCandidate(list[x],x);
      }
      
      this.setState({
				selectedCol:newCol,
        newKeys:newKeys,
        colKeys:col,
        candidateList:list
      });
			console.log(newCol,newKeys);
    }
    else{
      swal("Already Exists !","Coloumn Already Exists , Can't Add","error");
    }
     

}



getCandidates(){
  // for tabs primarystatus;
  $("#search_field").val("");
  let that=this;
  let RecuterNames=[];
  let refList=this.state.refObjKeys;
    // this.state.newKeys.slice();
    
  let colSelected=JSON.parse(JSON.stringify(this.state.selectedColRef));
  
  $.ajax({
    url:URL.NAUKARI_GET_CANDIDATE_BY_STATUS,
    type:"post",
    data:{
      "email":atob(localStorage.getItem("token")).split(":")[0],
      "status":this.state.activeStatus
      }
  }).done((data)=>{
    data=JSON.parse(data);
    let arr=[];
    console.log(data);
    for(let x in data.data)
    { data.data[x]["selected"]=false;
      data.data[x]["et/ec"]=data.data[x]["et/ec"]==undefined?"":data.data[x]["et/ec"];
       arr.push(data.data[x]);
       data.data[x].added_by.forEach((object)=>{
        if(object.userName&&RecuterNames.indexOf(object.userName)===-1){
          RecuterNames.push(object.userName);
        }
      });
    }
    data.data=arr;
    
    that.setState({
      candidateList:arr,
      recuterNames:RecuterNames,
      finalFilterList:[]
    });
  }).fail((e,ee,xhr)=>{
    swal("Error in candidates loading.","Error in loading candidates . ","error");
  });
}

