import React, { Component } from 'react';
// import LevelOne from './LevelOne.js';
const $=window.$;

class Actions extends Component {

    constructor(props){
        super(props);
        this.state={};
    }

    componentWillReceiveProps(nextProps){
    }

    render() {
        return (
            <div className="bg-white">
                <div>
                     <h2 className="" style={{marginTop:10}}>Candidates List</h2>
                </div>

                <div className="row options">
                    <div className="col-xs-12 col-sm-6 wid-xs-padd-top">

                      
                    <button className="btn btn-primary" style={{marginRight:10}}
                       data-toggle="modal" data-target="#uploadexcel_modal"
                       ><i className="fa fa-upload"></i>Upload Excel</button>

                    <button className="btn btn-primary" data-toggle="modal" 
                    data-target="#excel_modal"><i className="fa fa-download"></i>Download Excel</button>

                      </div>
                      <div className="col-xs-12 col-sm-6 wide-xs-padd-top text-right"> 
                        <input type="text" name="" id="search_field" className="form-control" placeholder="Search here"/>
                    </div>
                    </div>
                </div>
        );
    }


    componentDidMount(){
        $("#recruiter, #status, #substatus, #finalstatus").hide();
        $('#search_field').on('keyup',()=>{
            let patt = $("#search_field").val().toLowerCase();
            this.props.searchBy(patt);
        });
    }


}

export default Actions;



