import React, { Component } from 'react';


class ColModal extends Component {

    constructor(props){
        super(props);
        this.state={
            keys:this.props.colSelected
        };
    }

    componentWillReceiveProps(nextProps){ 
        // console.log(nextProps.colSelected)
        this.setState({keys: nextProps.colSelected });
    }

  render() {
      let obj=this.state.keys,headers=[];
    for(let x in obj){
        let temp={};
        temp[x]=obj[x];
         headers.push(temp);
    }
    return (
      <div>
      <div className="modal fade" id="excel_modal" tabIndex="1" role="dialog" aria-labelledby="myModalLabel" data-backdrop="static">
  <div className="modal-dialog" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={this.reset.bind(this)}><span aria-hidden="true">&times;</span></button>
        <h4 className="modal-title" id="myModalLabel">Select Coloumns</h4>
      </div>
      <div className="modal-body">
        <table className="table">
            <thead>
                <tr>
                    <th>#</th>
                    <th>Columns</th>
                </tr>
            </thead>
            <tbody>
                {headers.map((value,index)=>{
                    return (   
                <tr key={index}>
                    <td><input type="checkbox" checked={value[Object.keys(value)[0]]?true:false} onChange={this.onChangeKey.bind(this,Object.keys(value)[0])}/></td>
                    <td>{Object.keys(value)[0]}</td>
                </tr>
                    );
                })}
                
            </tbody>
        </table>  
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-default" data-dismiss="modal" onClick={this.reset.bind(this)}>Close</button>
        <button type="button" className="btn btn-success" id="excel-button" data-dismiss="modal" onClick={this.props.updateColSelected.bind(null,this.state.keys)}>Download Excel</button>
      </div>
    </div>
  </div>
</div>
      </div>
    );
  }

  reset(){
      this.setState({keys:this.props.colSelected});
  }

  onChangeKey(key){
    let obj =JSON.parse(JSON.stringify(this.state.keys));
    obj[key]=!obj[key];
    console.log(obj);
    this.setState({keys:obj});
  }

}

export default ColModal;



