import React, { Component } from 'react';
const $= window.$;
const limit=20;
const mrg={marginRight:5}
class Table extends Component {


    constructor(props){
        super(props);
        this.state={
             startIndex:1,
             endIndex:Math.ceil((this.props.candidateList.length||limit)/limit),
             currentIndex:1
        };
        this.loading=true;
    }

    componentWillReceiveProps(nextprops){
        // if(nextprops.candidateList.length>this.props.candidateList.length){
            // console.log("more length in list")
            // console.log()
            this.setState({
                    startIndex:1,
                    endIndex:Math.ceil((nextprops.candidateList.length>limit
                        ?nextprops.candidateList.length:limit)/limit),
                    currentIndex:1  
               }   );
        // }
        this.loading=false;
    }

    componentDidMount(){
        this.prevleft=0;
        window.onkeydown=(e)=>{
                if(e.keyCode==37){
                    this.prevleft=this.prevleft-10>0?this.prevleft-10:0;
                    $(".box-body").scrollLeft(this.prevleft);
                }
                else if(e.keyCode==39){
                    this.prevleft=this.prevleft+10<=$(".box-body .table").width()-$(".box-body").width()?this.prevleft+10:this.prevleft; 
                    $(".box-body").scrollLeft(this.prevleft);
                }
                // console.log(this.prevleft); 
        };
    }

    componentDidUpdate(){
        
        $("#col-view table>tbody>tr").off('click');
        $("#col-view table>tbody>tr").click(function(){
            // console.log(this)
            // alert("hii")
            $("#col-view table>tbody>tr").removeClass("highlight");
            $(this).toggleClass("highlight");
        });
    }
    componentWillUnmount(){
        window.onkeydown=null;
    }



  render(){


    return (
        <div id="col-view">
      {/* <button className="btn btn-primary" data-toggle="modal" data-target="#column_modal" style={{"float":"right","margin-bottom":"10px"}}>Add Column</button> */}
        <div className="box-body" style={{'overflow':'auto'}}>

                            <table className="table table-bordered table-hover table-striped" id="list">
                              <TableHeader selectAll={this.props.selectAll} 
                              updateColKeys={this.props.updateColKeys}
                              colKeys={this.props.colKeys} deselectAll={this.props.deselectAll} newKeys={this.props.newKeys} {...this.state}/>
                              <tbody id="main_list">
                              {/* {this.props.candidateList.length==0&&this.loading?
                              <tr><td colSpan="17" className="text-center" style={{fontSize:30,fontWeight:700}}><span><i className="fa fa-spin fa-spinner"></i> LOADING</span></td></tr>:null} */}

                                  {this.props.candidateList.map((value,index)=>
                                  { 
                                      let matchObj=value.added_by[0];
                                    //   console.log(matchObj)
                                    let statusList = matchObj.status;
                                    let finalStatus = statusList[statusList.length - 1].status,
                                    date=statusList[statusList.length-1].Timestamp;
                                    finalStatus=finalStatus.toString();
                                    if((finalStatus===""||finalStatus == null||finalStatus=="NaN"||finalStatus.includes("null"))
                                && statusList.length>1){
                                        finalStatus = statusList[statusList.length - 2].status;
                                        date=statusList[statusList.length-2].Timestamp;
                                    }
                                    finalStatus=finalStatus.split(":")
                                let upperlimit=this.state.currentIndex*limit;
                                let lowerlimit=(this.state.currentIndex-1)*limit;
                                // console.log(upperlimit, lowerlimit)
                                return index<upperlimit&&index>=lowerlimit?
                                (
                                  <tr key={index}>
                                   {/* <td>{value._id.$id}</td> */}
                                       <td><input type="checkbox" value={JSON.stringify(value)} 
                                       checked={value.selected?true:false} onClick={this.selectCandidate.bind(this,index)}/></td>
                                    
                                      {
                                          this.props.colKeys.map((elem,index)=>{
                                              let td=<td>{' '}</td>;
                                              switch(elem){
                                                case "Status":td=<td key={index}>{finalStatus[0]+' : '+finalStatus[1]+' '}</td>;
                                                            break;
                                                case "Last Action":td=<td key={index}>{new Date(parseInt(date)).toLocaleDateString()}</td>;
                                                              break;
                                                case "Name":td=<td data-toggle="modal" data-target="#resume_modal" 
                                                onClick={this.props.setCurrentCandidate.bind(null,value)}
                                                style={{color:"#0000EE"}} key={index}>{value.candidateName}</td>;
                                                    break;
                                                case "Mail Id":td=<td key={index}>{value.candidateEmail}</td>;break;
                                                case "Mobile":td=<td key={index}>{value.candidateContact}</td>;break;
                                                case "Recruiter":td=<td style={{textTransform:"capitalize"}} key={index}>{matchObj.userName}</td>;break;
                                                case "Company":td= <td key={index}>{value.work_experience[0].organization}</td>;break;
                                                case "Designation":td=<td key={index}>{value.work_experience[0].role}</td>;break;
                                                case "Experience":td=<td key={index}>{value.experience}</td>;break;
                                                case "CTC":td=<td key={index}>{value.current_ctc}</td>;break;
                                                case "ETEC":td=<td key={index}>{value["et/ec"]}</td>;break;
                                                case "Notice Period":td=<td key={index}>{value.noticePeriod}</td>;break;
                                                case "Univesity":td=<td key={index}>{value.college}</td>;break;
                                                case "Degree":td=<td key={index}>{value.degree}</td>;break;
                                                case "Location":td=<td key={index}>{value.location}</td>;break;
                                                default:td=<td key={index}>{value[elem]}</td>;
                                              }
                                              return td;
                                          })
                                      }
                                        <td></td>
                                  </tr>
                                      ):null;
                                  })
                                  }
                              </tbody>
                              
                              {/* <TableFooter newKeys={this.props.newKeys}
                              colkeys={this.props.colKeys} 
                              /> */}
                            </table>
                          </div>
                          <div style={{marginTop:20}} className="text-center">
                          <nav aria-label="Pagination" style={{display:"inline-block"}}>
                            <ul className="pagination">
                                <li className={this.state.currentIndex==this.state.startIndex?"disabled":""}
                                onClick={()=>{
                                    if(this.state.currentIndex-1>=this.state.startIndex)
                                   this.setState({currentIndex:this.state.currentIndex-1}) 
                                }}>
                                    <span>
                                    <span aria-hidden="true">&laquo;</span>
                                    </span>
                                </li>
                                {this.getPages.apply(this)}

                                <li className={this.state.endIndex==this.state.currentIndex?"disabled":""}
                                onClick={()=>{
                                    if(this.state.currentIndex+1<=this.state.endIndex)
                                   this.setState({currentIndex:this.state.currentIndex+1}) 
                                    
                                }}>
                                <span aria-label="Next">
                                    <span aria-hidden="true">&raquo;</span>
                                </span>
                                </li >
                            </ul>
                           </nav>

                          </div>
                          </div>
    );
  }


  selectCandidate(index){
      let list=JSON.parse(JSON.stringify(this.props.candidateList));
      list[index].selected=!list[index].selected;
      this.props.selectCandidate(list);
  }

  getPages(){
      let arr=[];
      for(let i=1;i<=this.state.endIndex;i++){
          if(i==this.state.currentIndex){
              arr.push(<li className="active" key={i}><span>{i}</span></li> )
          }
          else
        arr.push(
            <li onClick={()=>this.switchToPage(i)}  key={i}><span>{i}</span></li>
        )
      }
    //   console.log(arr)
      return arr;
  }

  switchToPage(pageNum){
    //   console.log(this.state,pageNum)
      this.setState({currentIndex:pageNum});
  }

}

export default Table;


const TableFooter= (props)=>{
    let keys=props.colkeys; 
                  
    return (<tfoot id="tfoot">
        <tr> 
          <th></th>
          {
              keys.map((elem,index)=>{
                return (
                <th key={index}>
                    {elem}
                </th>);
              })
          }
        </tr>
        </tfoot>);
}
class TableHeader extends Component{
    constructor(props){
        super(props);
        this.state={
            allSelected:false
        };
    }
    
    render(){
    return (<thead id="thead">
        <tr>
          <th className="check"><input type="checkbox" checked={this.state.allSelected?true:false} onClick={this.manageSelectAll.bind(this)}/></th>
          {
              this.props.colKeys.map((elem,index)=>{
                return (
                <th key={index} 
                draggable="true" className="draggable" onDragStart={(e)=>this.onDragStart(e,index)}
                  onDrop={(e)=>this.onDrop(e,index)} onDragOver={(e)=>this.onDragOver(e)}>
                    {elem}
                </th>);
              })
          }
          <th><button className="btn btn-primary" data-toggle="modal" data-target="#newCol_modal">Add Column</button></th>
         </tr>
        </thead>);
    }

    onDragOver(e,index){
        e.preventDefault();
    }
    onDragStart(e,dragElemIndex){
        e.dataTransfer.setData("elementIndex",dragElemIndex);
        console.log("drag start",dragElemIndex);
    }
    onDrop(e,toBeSwitchedWithIndex){
        e.preventDefault();
        let index=e.dataTransfer.getData("elementIndex");
        console.log("element of index ",index," dropped at ",toBeSwitchedWithIndex);
        
        let arr=JSON.parse(JSON.stringify(this.props.colKeys));
        let elem=arr[index];
        arr[index]=arr[toBeSwitchedWithIndex];
        arr[toBeSwitchedWithIndex]=elem;
        // console.log(arr);
        this.props.updateColKeys(arr);
    }


    manageSelectAll(){
        // console.log(this.props)
        if(this.state.allSelected)this.props.deselectAll();
        else this.props.selectAll((this.props.currentIndex-1)*limit,this.props.currentIndex*limit);
        this.setState({allSelected:!this.state.allSelected});
    }
}


