import React,{Component} from  'react';
const $=window.$;
export default class NavBar extends Component{

    render(){
        // window.resume=$.parseHTML(this.props.currentCandidate.resumeText.trim());
    return (
  <div className="nav_bar">
       <div id="logged_user" className="row no-mrg bg-white">
        <div className="user_img pull-left">
            <img src="assets/image/user.png" className="img-responsive img-circle" alt="User 1"/>
        </div>
        <div className="user_info pull-left">
            <h5 style={{textTransform:"capitalize"}}>{localStorage.getItem("name")||atob(localStorage.getItem("token")).split(":")[0].split("@")[0]||"Welcome User"}</h5>
            <h6>{atob(localStorage.getItem("token")).split(":")[0]}</h6>
        </div>
      </div>
      <div className="active_block">    
        <a className={this.props.activeStatus=="new:New Resume"?"bg-white active":"bg-white"} id="cand"
        onClick={()=>{this.manageTabs("new:New Resume");}}>
        New Resume</a>

        <a className={this.props.activeStatus=="new:Duplicacy Detected"?"bg-white active":"bg-white"} id="jb"
        onClick={()=>{this.manageTabs("new:Duplicacy Detected");}}>
        Duplicate</a>

      </div>
     
      {/* <h5>Active Jobs</h5>
      <div className="row active_jobs">
          <ul className="nav">
          {
              this.props.JobsList.map((obj,index)=>     
              <li key={index}><a href="javascript:void(0);">
              <i className="fa fa-briefcase" aria-hidden="true"></i> {obj.primarySkill} 
              <span className="">- {obj.clientName}</span></a></li>
         )
          }
          </ul>
      </div> */}
    </div>
    );
    }

    manageTabs(str){
        this.props.updateActiveStatus(str);
        $(".active_block .active").removeClass("active");
        if($(window).width()<=992)
        $(".nav_bar").addClass("translatey100");
    }

    componentDidUpdate(){
        
    }
}


