import React,{Component} from "react";
import URL from './../common/ApiConst';
import swal from 'sweetalert2';
const $=window.$;
export default class UploadExcel extends Component{

    constructor(props){
        super(props);
        this.state={
            resumeFile:new File([],"")
        };
    }

    getFileName(){
        let text = "";
        let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz";
    
        for (var i = 0; i < 20; i++){
          text += possible.charAt(Math.floor(Math.random() * possible.length));
        }
        let namearr=this.state.resumeFile.name.split(".");
        return text+"."+namearr[namearr.length-1];
    }    
    
  uploadResume(number,filename){
    let form =new FormData();
    form.append('file',this.state.resumeFile);
    form.append("filename",filename);
    form.append("number",number);
    
    
    let uploadProgress = function(evt) {
      console.log(evt)
      if (evt.lengthComputable){
          var percentComplete = Math.round(evt.loaded * 100 / evt.total);
          console.log(percentComplete);
        }
    }
    
    let uploadComplete = function(evt) {
        swal("Upload Complete","Resume updated successfully!","success");
      $("#updateresume_modal").modal('hide');
      console.log(evt.target.responseText,"uploaded");
    }
  
    let uploadFailed = function(evt) {
     console.log('Upload Failed');
    }
    
    var xhr = new XMLHttpRequest();
    xhr.upload.addEventListener("progress", uploadProgress, false);
			xhr.addEventListener("load", uploadComplete, false);
			xhr.addEventListener("error", uploadFailed, false);
      xhr.open("POST",URL.SAVE_RESUME_FILE);
      xhr.send(form);
  }

  changeFileResume(event){
    this.setState({
        resumeFile:event.target.files[0]
    });
    window.a=event.target.files[0];
  }

  uploadExcel(){
      if(!this.state.resumeFile){
      $("#updateresume_modal").modal('hide');  
        return;
      }
    let filename=this.getFileName();
    this.uploadResume(this.props.currentCandidate.candidateContact,filename);
    let candidate=JSON.parse(JSON.stringify(this.props.currentCandidate));
    candidate.resumeFileName=filename;
    this.props.updateCandidate(candidate,5);
    this.reset();
  }

  reset(){
    $("#rs2").val("");
    this.setState({resumeFile:new File([],"")});
  }

  render(){
      return (
    <div>
      <div className="modal fade" id="uploadexcel_modal" tabIndex="-120" role="dialog" aria-labelledby="myModalLabel40" data-backdrop="static">
         <div className="modal-dialog" role="document">
        <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
        <h4 className="modal-title" id="myModalLabel">Update Resume</h4>
      </div>
      <div className="modal-body">
      <div className="text-center row"> 
      <h2>
        Upload Excel
      </h2>
      
      <div className="form-group">
              
                                <div className="col-sm-12">
                                  <input type="file" className="form-control" name="resume" 
                                  accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                                   id="rs2" placeholder="Resume" onChange={(e)=>this.changeFileResume(e)}/>
                                </div>
                      </div>  
      </div>
            </div>
            <div className="modal-footer">
                <button type="button" className="btn btn-default" data-dismiss="modal" onClick={()=>this.reset()}>Cancel</button>
                <button type="button" className="btn btn-success"  
                // onClick={this.UploadExcel.bind(this)}
                >Save</button>
            </div>
            </div>
        </div>
        </div>
    </div>
      );
  }


}


